body{
  margin-top: 0;
  margin-bottom: 0;
}

.center {
  max-width: 1600px;
  margin: 0 auto;
  // padding: 0 30px;
  // position: relative;
  z-index: 2;
}


.header{
  text-align: center;
//   margin-bottom: 40px;
  padding: 0px 0;
  background:
    linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)),
    url('../../assets/man-getting-his-hair-trimmed-with-trimmer4-min.jpg');
  background-size: cover;
  background-position: center;
  min-height: 485px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  

.title {
  font-size: 90px;
  color: #FFF;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
  text-transform: uppercase;
  opacity: 0;
  transform: translateY(10%) scale(0.7);
  filter: blur(2px);
  perspective: 500px; 
  transition: all 1.5s cubic-bezier(0.4, 0, 0.2, 1);
  
  &.active {
    opacity: 1;
    transform: translateY(0) scale(1);
    filter: blur(0);
  }

        @media (max-width: 1200px) {
            font-size: 70px;
        }

        @media (max-width: 820px) {
            font-size: 50px;
        }

        @media (max-width: 480px) {
            font-size: 36px;
            margin-bottom: 20px;
            letter-spacing: 2;
        }
  }

}

.main{
        .flexAbout {
  background: #1a1a1a;
  padding: 30px 20px 68px 20px;
  color: #fff;

  @media (min-width: 768px) {
    padding: 20px 30px 60px 30px;
  }
}

.flexAbout__contentTitle {
  font-size: 48px;
  font-weight: 700;
  text-align: center;
  margin-bottom: 80px;
  max-width: 900px;
  margin-left: auto;
  margin-right: auto;
  opacity: 0;
  transform: translateY(10%) perspective(500px) translateZ(-200px) scale(0.7);
  filter: blur(2px);
  transition: all 1s ease-in-out;

  &.active {
    opacity: 1;
    transform: translateY(0) perspective(500px) translateZ(0) scale(1);
    filter: blur(0);
  }
}

.flexAboutFlex {
  display: flex;
  gap: 120px;
  align-items: flex-start;
  padding-bottom: 30px;
}

.flexAbout__img {
  flex: 1;
    margin-left: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 50px;
    opacity: 0;
    transform: translateX(-100%);
    transition: all 1.5s ease-in-out;
  
    &.active {
      opacity: 1;
      transform: translateX(0);
    }

  img {
    width: 260px;
    height: 268px;
    display: block;
    object-fit: cover;
  }
}

.flexAbout__content {
  flex: 2;
  padding-top: 20px;
  opacity: 0;
  transform: translateX(100%);
  transition: all 1.5s ease-in-out;

  &.active {
    opacity: 1;
    transform: translateX(0);
  }
}

.phoneNumber {
  display: inline-block;
  font-size: 20px;
  color: #B8A269;
  font-weight: 700;
  text-decoration: none;
  margin-bottom: 0;
}

.flexAbout__content h3 {
  font-size: 36px;
  font-weight: 500;
  margin-bottom: 30px;
  position: relative;
  margin-top: 20px;
}

.flexAbout__content h3::after {
  content: "";
  display: block;
  width: 120px;
  height: 2px;
  background: #B8A269;
  margin-top: 20px;
}

.hours {
  display: flex;
  justify-content: space-between;
  max-width: 300px;
  margin-bottom: 20px;
  font-size: 20px;

  b {
    font-weight: 600;
    font-size: 20px;
  }

  span {
    font-size: 20px;
    color: #ddd;
  }
}

.barberDescription {
  margin-top: 60px;
  font-size: 16px;
  color: #ffffff;
  line-height: 1.6;
  padding-bottom: 20px;
}

  i {
    font-style: italic;
  }


@media (max-width: 1024px) {

  .flexAboutFlex {
    flex-direction: column;
    padding: 30px;
  }

  .flexAbout__img {
    margin: 0 auto;
  }

  .flexAbout__contentTitle {
    font-size: 36px;
    margin-bottom: 50px;
  }

  .flexAbout__content {
    padding-top: 0;
  }
}

@media (max-width: 600px) {
    .flexAbout{
        padding-top: 30px;
    }
    .flexAboutFlex {
    padding: 0px;
  }

  .flexAbout__contentTitle {
    font-size: 28px;
  }

  .flexAbout__content h3 {
    font-size: 26px;
  }
}


.slide {
    overflow: hidden;

  img {
    width: 100%;
    height: 370px;
    object-fit: cover;
    transition: all 0.4s ease;

    @media (max-width: 1024px) {
      height: 300px;
    }

    @media (max-width: 768px) {
      height: 380px;
    }

  }

  &:hover {
    img {
      transform: scale(1.1);
    }
  }
}

  :global(.slick-track) {
    display: flex;
    gap: 10px;
  }

  :global(.slick-slide) {
    height: auto;

    > div {
      height: 100%;
    }
  }

:global(.slick-prev),
:global(.slick-next) {
  width: 50px;
  height: 50px;
  z-index: 10;
}

:global(.slick-prev) {
  left: 20px;
}

:global(.slick-next) {
  right: 20px;
}

:global(.slick-prev:before),
:global(.slick-next:before) {
  font-size: 40px;
  color: #4d4d4c;
    opacity: 0.8;
    font-weight: 1000;
  }

:global(.slick-prev:hover:before),
:global(.slick-next:hover:before) {
  color: #aaa !important;
  opacity: 0.9 !important;
}
 
}

